<template>
  <div id="app">
    <loading class="loading" v-if="loading"></loading>
    <div class="header">
      <nav-head></nav-head>
    </div>
    <div class="wrapper">
      <nav-side></nav-side>
      <content-wrap></content-wrap>
    </div>
    <div v-show="showFooter" class="footer-wrap">
      <footer-wrap></footer-wrap>
    </div>
    <cnode-login v-if="$store.state.openLoginCard"></cnode-login>
    <!-- githubfork -->
    <a v-show="showFooter" href="https://github.com/Reviving-Pain/reviving-pain.github.io" target="_blank">
      <img class="fork-on-github" src="./assets/fork_on_github.png" alt="Fork me on GitHub">
    </a>
  </div>
</template>

<script>
import navHead from './components/navHead.vue'
import navSide from './components/navSide.vue'
import contentWrap from './components/contentWrap.vue'
import cnodeLogin from './components/cnodeLogin.vue'
import footerWrap from './components/footerWrap.vue'
import loading from './components/common/loading.vue'
import request from './util/apiRequest.js'

export default {
  components: {
    'nav-head': navHead,
    'nav-side': navSide,
    'content-wrap': contentWrap,
    'cnode-login': cnodeLogin,
    'footer-wrap': footerWrap,
    'loading': loading
  },
  data: function () {
    return {
      // 
      defaultHeight: document.documentElement.clientHeight,
      // 
      currentHeight: document.documentElement.clientHeight,
      // footer
      showFooter: true,
      loading: false,
      isMobil: true,
      mobilDirection: 0
    }
  },
  methods: {
    // footer
    resizeMethod: function () {
      // 
      if (navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      )) {
        // pcfooter
        if (this.isMobil === true) {
          // onresize
          this.isMobil = true
          // defaultHeight()currentHeight()
          if (window.orientation !== this.mobilDirection) {
            this.mobilDirection = window.orientation
            this.defaultHeight = document.body.clientHeight
            this.currentHeight = document.body.clientHeight
          }
          // footer,
          this.currentHeight = document.body.clientHeight
          if (this.defaultHeight - this.currentHeight > 100) {
            this.showFooter = false
          } else {
            this.showFooter = true
          }
        } else {
          // do nothing, pcmobil, footer
        }
      } else {
        // PC
        this.isMobil = false
      }
    },
    // accesstoken
    verifyToken: function () {
      request.verifyAccesstoken({
        accesstoken: sessionStorage['accesstoken']
      }, (res) => {
        this.loading = false
        this.$store.commit('changeLoginStatus', res.data)
      }, (err) => {
        this.$store.commit('changeLoginStatus', {
          success: false
        })
        alert('' + err.response.data.error_msg)
        // 
        this.$store.commit('openLoginCard', false)
        this.$nextTick(() => {
          this.$store.commit('openLoginCard', true)
        })
        sessionStorage['accesstoken'] = ''
      })
    }
  },
  created: function () {
    // token
    if (sessionStorage['accesstoken']) {
      this.loading = true
      this.verifyToken()
    } else {
      this.$store.commit('openLoginCard', true)
    }
  },
  mounted: function () {
    // resize
    window.addEventListener('resize', this.resizeMethod, true)
    this.resizeMethod()
  }
}
</script>

<style>
html, body {
  height: 100%;
  margin: 0px;
  padding: 0px;
  font-size: 15px;
  /**/
  -webkit-tap-highlight-color: transparent;
}
/**/
::-webkit-scrollbar {
  /*  */
  width: 8px;
  /*  */
  height: 8px;
}
/* ++*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.1);
  background-color: #c4c4c4;
}
/* ++*/
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 8px;
  background-color: #F5F5F5;
}

#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.header {
  height: 60px;
}
.wrapper {
  flex: 1;
  position: relative;
  overflow: hidden;
}
.footer-wrap{
  height: 50px;
}
.fork-on-github {
  width: 120px;
  height: 120px;
  border: 0;
  position: absolute;
  bottom: 50px;
  right: 0;
  z-index: 120;
}
.loading {
  z-index: 300 !important;
}
</style>



