<template>
  <div class="publishBackground">
    <div class="publishWrp">
      <!--  -->
      <div class="componentTitle">
        <slot name="modifyBlockTitle">
          <span></span>
        </slot>
      </div>
      <div class="selectType">
        <span></span>
        <select name="topicTab" v-model="publishData.topicTab">
          <option value="default_select"></option>
          <option value="share"></option>
          <option value="ask"></option>
          <option value="job"></option>
          <option value="dev"></option>
        </select>
      </div>
      <div class="topicTitle">
        <input type="text" v-model.trim="publishData.topicTitle" placeholder="10">
      </div>
      <div class="topicContent">
        <textarea v-model.trim="publishData.topicContent" placeholder="(markdown) "></textarea>
        <span></span>
        <vue-markdown :source="publishData.topicContent.replace('](www', '](https://www')" class="markdownDisplay"></vue-markdown>
      </div>
      <!-- newPublish -->
      <button v-if="!isModify" :disabled="subLoading" @click="publishTopic('newPublish')" class="publishBtn">
        <span v-if="!subLoading"></span>
        <span v-if="subLoading"><span class="fa fa-spinner fa-spin"></span> </span>
      </button>
      <!-- modifyPublish -->
      <button v-if="isModify" :disabled="subLoading" @click="publishTopic('modify')" class="publishBtn">
        <span v-if="!subLoading"></span>
        <span v-if="subLoading"><span class="fa fa-spinner fa-spin"></span> </span>
      </button>
    </div>
  </div>
</template>

<script>
import vueMarkdown from 'vue-markdown'
import request from '../../../util/apiRequest.js'
import commonUtil from '../../../util/common.js'

export default {
  props: {
    'topicTab': String,
    'topicTitle': String,
    'topicContent': String
  },
  components: {
    'vue-markdown': vueMarkdown
  },
  data: function () {
    return {
      subLoading: false,
      publishData: {
        topicTab: 'default_select',
        topicTitle: '',
        topicContent: ''
      },
      // 
      isModify: false,
      // urlhttps
      inputUrlPrefix: ''
    }
  },
  methods: {
    // or()
    publishTopic: function (publishType) {
      if (!(this.publishData.topicTab === 'default_select')) {
        if (this.publishData.topicTitle) {
          // 10~50
          var re = new RegExp('^.{10,50}$')
          if (re.test(this.publishData.topicTitle)) {
            console.log('')
            if (this.publishData.topicContent) {
              // url'www''https://www'
              this.publishData.topicContent = this.publishData.topicContent.replace('](www', '](https://www')
              if (publishType === 'newPublish') {
                if (confirm('')) {
                  this.subLoading = true
                  this.createTopic()
                } else {
                  console.log('')
                }
              } else if (publishType === 'modify') {
                if (confirm('')) {
                  this.subLoading = true
                  this.modifyTopic()
                } else {
                  console.log('')
                }
              } else {
                console.log('')
              }
            } else {
              alert('')
            }
          } else {
            alert('')
          }
        } else {
          alert('')
        }
      } else {
        alert('')
      }
    },
    // 
    createTopic: function () {
      request.createTopic({
        accesstoken: sessionStorage['accesstoken'],
        title: this.publishData.topicTitle,
        tab: this.publishData.topicTab,
        content: this.publishData.topicContent
      }, (res) => {
        this.subLoading = false
        if (confirm('')) {
          this.$router.push({path: '/cnodeCommunity/cnodejsTopic/' + res.data.topic_id})
        } else {
          // 
          this.$parent.reload()
        }
      }, (err) => {
        alert(err.response.data.error_msg)
      })
    },
    // 
    modifyTopic: function () {
      var topicId = this.$route.path.split('/').pop()
      request.modifyTopic({
        accesstoken: sessionStorage['accesstoken'],
        topic_id: topicId,
        title: this.publishData.topicTitle,
        tab: this.publishData.topicTab,
        content: this.publishData.topicContent
      }, (res) => {
        this.subLoading = false
        this.$parent.$parent.reload()
        console.log()
      }, (err) => {
        alert(err)
      })
    }
  },
  created: function () {
    // 
    if (this.$route.path.split('/').pop() === 'createTopic') {
      commonUtil.exchangePageTitle('CnodeJS')
    }
    // 
    if (this.topicTab) {
      this.isModify = true
      this.publishData.topicTab = this.topicTab
      this.publishData.topicTitle = this.topicTitle
      this.publishData.topicContent = this.topicContent
    }
  }
}
</script>

<style scoped>
.publishBackground {
  min-height: 100%;
  padding: 0px 10px 0px 10px;
  background: linear-gradient(#f6f6f6, #fff);
  overflow: hidden;
}
.publishWrp {
  max-width: 800px;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 5px;
  padding: 10px;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  background: #fff;
}
.publishWrp .componentTitle {
  border-bottom: 1px solid #999;
  padding-bottom: 10px;
  margin: 10px;
  font-size: 1.3rem;
  font-weight: bold;
  color: #333;
}
.selectType {
  margin: 10px ;
  font-size: 1.2rem;
}
.selectType select {
  width: 150px;
  height: 30px;
  padding-left: 5px;
  border-radius: 5px;
  font-size: 1.2rem;
  background: #fff;
}
.topicTitle {
  padding: 10px 20px 10px 10px;
}
.topicTitle input {
  width: 100%;
  height: 30px;
  padding-left: 10px;
  border: 1px solid #aaa;
  border-radius: 5px;
  font-size: 1.2rem;
  outline: none;
}
.topicContent {
  padding: 10px 23px 10px 10px;
}
.topicContent textarea {
  width: 100%;
  height: 260px;
  padding-left: 10px;
  border: 1px solid #aaa;
  border-radius: 5px;
  margin-bottom: 10px;
  font-size: 1.1rem;
  font-family: '';
  outline: none;
  resize: none;
}
.topicContent .markdownDisplay {
  width: 100%;
  height: 260px;
  padding-left: 10px;
  border: 1px solid #aaa;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 5px;
  margin: 5px 0px 10px 0px;
  font-size: 0.8rem;
  font-family: '';
  overflow-y: auto;
}
.topicContent .markdownDisplay >>> img {
  max-width: 85%;
  display: block;
  margin: 0 auto;
}
.publishBtn {
  width: 100%;
  max-width: 600px;
  height: 50px;
  line-height: 50px;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  background: #c60023;
  display: block;
  text-align: center;
  color: #fff;
  font-size: 1.3rem;
  cursor: pointer;
  user-select: none;
}
</style>



