// mockjs
const Mock = require('mockjs')
// Ajax
Mock.setup({
  timeout: 0 - 300
})
// 
export var data = Mock.mock({
  'list|1-10': [{
    'id|+1': 1
  }],
  'name|3': [{
    first: '@FIRST',
    middle: '@FIRST',
    last: '@LAST',
    full: '@first @middle @last'
  }]
})



